.backdrop {
    background-color: rgba(0, 0, 0, 0.8);
    bottom: 0;
    height: 100vh;
    width: 100%;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 500;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s;

    &.active {
        opacity: 1;
        visibility: visible;
    }
}
